.menu {
  position: fixed;
  box-sizing: border-box;
  height: 100vh;
  background-color: #C2C2C2;
  font-weight: 600;
  text-transform: uppercase;
  overflow: auto;

  &__top {
    position: sticky;
    z-index: 1;
    margin-bottom: 24px;

  @include on-tablet {
    margin-bottom: 36px;
    }
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    font-size: 16px;
    line-height: 27px;
    color: $main-color;
    text-decoration: none;
  }

  &__book-test-ride {
    display: block;
    width: fit-content;

    padding-bottom: 7px;
    border-bottom: 1px solid #1D1D1D;

    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    color: $main-color;
    text-decoration: none;
  }
}
